<template>
  <div>
    <div>
      <div style="margin: 0 0;margin-bottom: 10px">
        <el-button type="danger" @click="delbath()">批量删除<i class="el-icon-remove" style="margin-left: 5px"></i>
        </el-button>
        <el-upload
            class="upload-demo ml-10"
            :action="'http://' + serverIp +':9090/file/upload/'" :show-file-list="false" accept=".xlsx"
            :on-success="handleFileUploadSuccess" style="display: inline-block;">
          <el-button type="primary"> 导入<i class="el-icon-bottom" style="margin-left: 5px"></i>
          </el-button>
        </el-upload>
      </div>
      <el-table :data="fileData" border stripe :header-cell-class-name="headerBg"
                @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="name" label="文件名" show-overflow-tooltip></el-table-column>
        <el-table-column prop="type" label="文件类型" width="120"></el-table-column>
        <el-table-column prop="size" label="文件大小(kb)"></el-table-column>
        <el-table-column prop="download" label="下载">
          <template slot-scope="scope">
            <el-button type="primary" @click="download(scope.row.url)">下载</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="enable" label="启用">
          <template slot-scope="scope">
            <el-switch
                v-model="scope.row.enable"
                active-color="#13ce66"
                inactive-color="#ccc"
                @change="changeEnable(scope.row)"
            >
            </el-switch>
          </template>


        </el-table-column>
        <el-table-column label="操作" v-slot="scope" min-width="83px" style=" display:inline-block;">
          <el-button type="danger" @click="del(scope.row.id)" style="margin-left: 0">删除<i class="el-icon-remove"
                                                                                          style="margin-left: 3px"></i>
          </el-button>
        </el-table-column>
      </el-table>

    </div>
    <div style="padding: 10px 0">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[2, 5, 10, 20]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
import {serverIp} from "../../public/config";

export default {
  name: "File",
  data() {
    return {
      fileData: [],
      headerBg: 'headerBg',
      enable: true,
      total: 0,
      pageNum: 1,
      pageSize: 10,
      multipleSelection: [],
      name: "",
      serverIp: serverIp
    }
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      this.request.get("/file/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.name,
        }
      }).then(res => {
        console.log(res)
        this.fileData = res.records
        this.total = res.total
      })
    },
    download(url) {//下载文件
      window.open(url)
      console.log("download()==>url==>" + url)
    },

    handleSelectionChange(val) {//控制多选
      console.log(val)
      this.multipleSelection = val
    },
    handleSizeChange(pageSize) {
      console.log(pageSize)
      this.pageSize = pageSize
      this.load()
    },
    handleCurrentChange(pageNum) {
      console.log(pageNum)
      this.pageNum = pageNum
      this.load()
    },

    //启用禁用文件按钮
    changeEnable(row) {
      this.request.post("/file/update", row).then(res => {
        if (res === 1) {
          this.$message.success("操作成功!")
        }
      })
    },

    //删除文件
    del(id) {
      this.$confirm('此操作将永久删除该文件信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.request.delete("/file/" + id).then(res => {
          console.log("delRes==>" + res)
          this.load()
          if (res === 1) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
          }
        })


      })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
    },

    //批量删除文件
    delbath() {
      this.$confirm('此操作将永久删除文件信息, 是否继续?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let ids = this.multipleSelection.map(v => v.id)
        this.request.post("/file/del/batch", ids).then(res => {
          console.log("delRes==>" + res)
          if (res > 0) {
            this.$message({
              type: 'success',
              message: '删除成功!共删除' + res + '个文件',
            })
            this.load()
          } else {
            this.$message({
              type: 'info',
              message: '请至少选择一个文件进行删除'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },

    //导入用户信息成功提示
    handleFileUploadSuccess() {
      this.$message.success("用户信息上传成功!")
      this.load()
    },


  }
}
</script>

<style>
.headerBg {
  background-color: #eee !important;
}
</style>
